<template>
  <div class="login">
    <div >
        <mu-tabs class="login-head"  value="activeTab" @change="handleTabChange">
            <mu-tab  value="tab1"><span class="titlesize">登陆</span></mu-tab>
            <mu-tab value="tab2" ><span class="titlesize">注册</span></mu-tab>
        </mu-tabs>
    </div>
    <div class="login-content">
        <div v-if="activeTab === 'tab1'">
          <ul>
            <li><mu-text-field hintText="用户名" v-model="zhihuL.user" type="text" icon="group" fullWidth/></li>
             <li>  <mu-text-field hintText="密码" type="password" v-model="zhihuL.pwd" icon="vpn_key" fullWidth/></li>
              <li> <mu-radio label="记住密码" name="group" v-model="zhihuL.remember" nativeValue="simple1"  class="demo-radio"/></li>
              <li>
                 <mu-raised-button label="登陆" @click='login' fullWidth  primary/>
              </li>
          </ul>
        
        
          <br/>
         
        </div>
        <div v-if="activeTab === 'tab2'">
            <ul>
            <li><mu-text-field hintText="用户名" v-model="zhihuR.user" type="text" icon="group" fullWidth/></li>
             <li>  <mu-text-field hintText="密码" v-model="zhihuR.pwd" type="password" icon="vpn_key" fullWidth/></li>
    
              <li>
                 <mu-raised-button label="注册" @click='register' primary fullWidth/>
              </li>
          </ul>
        </div>
    </div>
    <footer>
  <mu-raised-button  label="回到首页-游客模式"   icon="home" @click='goback' fullWidth  secondary/>
    </footer>
     
</div>
 


</div>
</template>

<script>
// import store from '../vuex/store.js'
export default {
  name: 'hello',
  data () {
    return {
      activeTab: 'tab1',
      zhihuL: {
        user: '',
        pws: '',
        remember: 'false'
      },
      zhihuR: {
        user: '',
        pws: ''
      }
    }
  },
  methods: {
    handleTabChange (val) {
      this.activeTab = val
    },
    login () {
      let options = {
        user: this.zhihuL.user,
        pwd: this.zhihuL.pwd,
        remember: this.zhihuL.remember
      }
      this.$http('', options, function (res) {
            // 根据返回值 判断是否跳转
            // 成功 返回
            // 写入本地存储
        this.$router.go(-1)
        // 失败 继续登陆
        this.zhihu.pwd = ''
      }, function (res) {
        console.log(res)
      })
    },
    register () {
      let options = {
        user: this.zhihuR.user,
        pwd: this.zhihuR.pwd
      }
      this.$http('', options, function (res) {
            // 根据返回值 判断是否跳转
            // 成功 返回登陆页面
        this.activeTab = 'tab1'
        // 失败 继续登陆

        this.zhihuL.user = this.zhihuR.user
      }, function (res) {
        console.log(res)
      })
    },
    goback () {
      this.$router.go(-1)
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login{
width: 100%;
height: 100%;
  
}
.titlesize{
  font-size: 20px;
}
.login-head{
  height: 60px;
}
.login-content{
  margin-top: 30px;
}
.login-content ul{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.login-content ul li {
  width: 60%;
  margin-top: 20px;
  list-style: none;
}
.login footer{
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
</style>



