<template>
  <div class="theme"> 
      <mu-card-media :title="item.description" :subTitle="item.name">
    <img :src="IMG" />
  </mu-card-media>
  <div class="theme-author">
    <mu-list>
      <mu-list-item title="图片" disabled>
         <mu-avatar slot="left" :src="myron"/>
      </mu-list-item>
    </mu-list>
  </div>
      <ul class='theme-ul'> 
          <li  v-for="item in item.stories" ::key="item.id">
         <mu-paper class="demo-paper" :zDepth="2" >
              <router-link :to="{ name:'news',params: { id: item.id }}">
                <div class="theme-item">
                    <p><span>{{item.title}}</span> </p>
                    <img :src="myron" alt="">
                </div>     
               </router-link>
          </mu-paper>
          </li>   
      </ul> 
       <mu-paper class="demo-paper" :zDepth="2" >
            
      </mu-paper>
  </div>
  
</template>

<script>
import IMG from '../../static/img/banner.jpg'
import myron from '../../static/img/1.jpg'
export default {
  name: 'hottheme',
  data () {
    return {
      item: '1',
      myron,
      IMG

    }
  },
  mounted () {
    var url = null
    this.$store.dispatch('setLoad', true)
    url = '/api/4/news/' + 'latest'

    this.$http.get(url, {progress: function (event) {
              // console.log(event);
              // console.log(i++);
    }
    }).then(function (res) {
      this.item = res.body
      console.log(res.body)
      this.$store.dispatch('setLoad', false)
    }, function (res) {
      console.log(res)
    })
    window.addEventListener('onmousewheel ', function () {
      console.log(1)
    })
  },
// winWidth = document.body.clientWidth;
  computed: {

  },
  methods: {

  },
  updated () {

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped>
@import '/static/style/theme.css';

</style>



