<template>
<div class="news">
<link rel="stylesheet" :href="css">
<header class="news-head">
        <mu-appbar>
          <mu-icon-button icon='arrow_back' slot="left" @click='goback'/>
          <mu-icon-button icon='star' color="#pinkA200"  slot="right" @click='collects'/>
          <mu-icon-button icon='share' slot="right"/>
            <mu-badge :content="bedger.comments" circle secondary slot="right">
                 <mu-icon-button icon='sms' /> 
            </mu-badge>
             <mu-badge :content="bedger.popularity" circle secondary slot="right">
                 <mu-icon-button icon='thumb_up' /> 
            </mu-badge>
        </mu-appbar>
</header>
  <div class="news-content" v-html="item.body" > 
   
   
  </div>
 
</div>

</template>

<script>
export default {
  name: 'news',
  data () {
    return {
      bedger: {
        comments: '',
        popularity: ''
      },
      item: '',
      css: ''
    }
  },
  mounted () {
    var id = this.$route.params.id
    console.log(id)
    this.$http.get('/api/4/news/' + id).then(function (res) {
      this.item = res.body
      console.log(res.body, '----')
      return this.$http.get('/api/4/story-extra/' + id)
    }, function (res) {
      console.log(res)
    }).then(function (res) {
      console.log(res.body, '+++')
      this.bedger = {
        comments: res.body.comments.toString(),
        popularity: res.body.popularity.toString()
      }
      console.log(typeof this.bedger.comments)
    }, function (res) {
      console.log(res)
    })
  },

  methods: {
    goback () {
    //    windows.history.go(-1);
      window.history.go(-1)
    },
    collects () {
    // 判断是否登陆
    // 登陆则收藏
    // 否则跳转路由
      this.$router.push({name: 'login'})
      this.$router.go(1)
    }
  }


}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped>
    @import  '../../../static/style/news.css'
</style>



